.main__section {
    padding: 0 70px;
    max-width: 100%;
    min-height: 215px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    border-bottom: 1px solid #f0f0f0;
    a {
        margin-left: 20px;
        border-right: 1px solid #f0f0f0;
        &:last-child {
            border-right: none;
        }
        &:hover {
        text-decoration: none;
        opacity: 1;
        .main__section-card-link {
            text-decoration: underline;
            opacity: 0.7;
        }
        }
        .main__section-card {
            min-height: 215px;
            padding: 57px 20px 51px 20px;
            .main__section-card-logo {
            font-size: 40px;
            margin-bottom: 20px;
        }
        .main__section-card-link {
            text-transform: uppercase;
            font-weight: 18px;
            font-weight: bold;
            margin-bottom: 15px;
        }
        .main__section-card-text {
            font-weight: 300;
            letter-spacing: normal;
        }
        }
    }
}

@media screen and (max-width: 900px) {
    .main__section {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        padding: 0;
    }
}

@media screen and (max-width: 740px) {
    .main__section {
        grid-template-columns: 1fr ;
        grid-template-rows: 1fr 1fr 1fr 1fr;
        border-bottom: 1px solid #f0f0f0;
        a {
            border-bottom: 1px solid #f0f0f0;
            
            .main__section-card {
                    min-height: 64px;
                    padding: 0px;
                    .main__section-card-logo {
                        display: inline-block;
                        font-size: 30px;
                        vertical-align: bottom;
                }
                .main__section-card-link {
                    display: inline-block;
                    font-weight: 18px;
                    line-height: 64px;
                    margin-bottom: 0px;
                }
                .main__section-card-text {
                    display: none;
                }
            }
        }
    }
}